import { Link } from "react-router-dom";
import "./Products.css";

import { setCartItems } from "../../utils/cartActions";
import ProductsLoader from "./ProductsLoader";

const CategoryProducts = ({ status, error, products, category }) => {
  return (
    <>
      {status === "success" && (
        <div className="container-fill products__list">
          <div className="container__label">
            Products / {category && category.name}
          </div>
          <div className="products__content">
            {products &&
              products.reverse().map((product, index) => (
                <div className="product__item" key={index}>
                  <div className="product__item_header">
                    <div></div>
                    <div>-{product.discount}%</div>
                  </div>
                  <div className="product__item_cover">
                    <Link to={`/product/${product.slug}/`}>
                      <img
                        src={`https://web-production-741e.up.railway.app${product.cover1}`}
                        alt={product.name}
                      />
                    </Link>
                  </div>
                  <div className="product__item_info">
                    <div className="product__item_label">
                      <Link to={`/product/${product.slug}`}>
                        {product.name}
                      </Link>
                    </div>
                    <div className="product__item_price">
                      <div
                        className="product__item_atc"
                        onClick={() => setCartItems(product.id, 1)}
                      >
                        add to cart
                      </div>
                      <div className="product__item_prices">
                        <strike>${product.price}</strike> $
                        {product.price -
                          (product.price / 100) * product.discount}
                      </div>
                    </div>
                  </div>
                </div>
              ))}
          </div>
        </div>
      )}

      {status === "loading" && (
        <div className="container-fill products__list">
          <div className="container__label">Products</div>
          <ProductsLoader />
        </div>
      )}
      {status === "failed" && <p>{error}</p>}
    </>
  );
};

export default CategoryProducts;
