import { toast } from "react-toastify";
import { cartItemAdd } from "../Redux/Products/CartSlice";
import { store } from "../Redux/store";

export const getCartItems = () => {
  const products = JSON.parse(localStorage.getItem("products"));
  if (products) {
    store.dispatch(cartItemAdd(products));
  }
};

const notify = (msg) =>
  toast.success(msg, {
    position: "top-right",
    autoClose: 2000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
    theme: "light",
  });

export const setCartItems = (id, amount) => {
  let isAdded;
  let product = { id: id, amount: amount };
  let products = JSON.parse(localStorage.getItem("products"));

  if (products) {
    isAdded = products.find((product) => product.id === id);
    if (isAdded) {
      product = { id: id, amount: Number(isAdded.amount) + amount };
      products[products.indexOf(isAdded)] = product;
    } else {
      products.push(product);
    }

    products = JSON.stringify(products);
    localStorage.setItem("products", products);
  } else {
    products = JSON.stringify([product]);
    localStorage.setItem("products", products);
  }
  store.dispatch(cartItemAdd(JSON.parse(products)));
  if (amount > 0) {
    notify("Product added successfuly");
  } else {
    notify("Product removed successfuly");
  }
};

export const removeCartItem = (id) => {
  let products = JSON.parse(localStorage.getItem("products"));
  if (products) {
    products = products.filter((product) => product["id"] !== Number(id));
    products = JSON.stringify(products);
    localStorage.setItem("products", products);
  }
  store.dispatch(cartItemAdd(JSON.parse(products)));
  notify("Product removed successfuly");
};
