import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";

const initialState = {
  categories: [],
  status: "idle",
  errors: null,
};

export const fetchCategories = createAsyncThunk(
  "categories/fetchCategories",
  async () => {
    const response = await axios.get("/api/categories/");
    return response.data;
  }
);

const categoriesSlice = createSlice({
  name: "categories",
  initialState,
  reducers: {
    categoryCreationStart: (state) => {
      state.status = "loading";
    },
    categoryCreationSuccess: (state, action) => {
      state.status = "success";
      state.categories.push(action.payload);
    },
    categoryCreationFailed: (state, action) => {
      state.status = "failed";
      state.errors = action.error.message;
    },
  },
  extraReducers(builder) {
    builder
      .addCase(fetchCategories.pending, (state) => {
        state.status = "loading";
      })
      .addCase(fetchCategories.fulfilled, (state, action) => {
        state.status = "success";
        state.categories = action.payload;
      })
      .addCase(fetchCategories.rejected, (state, action) => {
        state.status = "failed";
        state.errors = action.error.message;
      });
  },
});

export const {
  categoryCreationFailed,
  categoryCreationStart,
  categoryCreationSuccess,
} = categoriesSlice.actions;

export const getCategories = (state) => state.categories.categories;
export const getCategoriesStatus = (state) => state.categories.status;
export const getCategoriesErrors = (state) => state.categories.errors;

export const getCategoriesBySlug = (state, slug) =>
  state.categories.categories.find((category) => category.slug === slug);

export const getCategoriesById = (state, id) =>
  state.categories.categories.find((category) => category.id === id);

export default categoriesSlice.reducer;
