import React, { useCallback, useState } from "react";
import "./Add.css";
import {
  creationStart,
  creationSuccess,
  creationStatus,
  creationError,
  creationFailure,
} from "../Redux/Products/Products";
import { useDispatch, useSelector } from "react-redux";
import { useHistory } from "react-router-dom";
import axios from "axios";

const AddProduct = () => {
  const navigate = useHistory();
  const dispatch = useDispatch();
  const errors = useSelector(creationError);
  const status = useSelector(creationStatus);
  const [validation, setValidation] = useState();

  const errorMessage = useCallback(() => {
    return Object.keys(errors).map((name) => {
      const msg = errors[name].join(", ");
      return `${name} - ${msg}`;
    });
  }, [errors]);

  const handleSubmit = async (e) => {
    e.preventDefault();
    dispatch(creationStart());
    try {
      if (
        e.target.name.value &&
        e.target.description.value &&
        e.target.slug.value &&
        e.target.amount.value &&
        e.target.category.value &&
        e.target.price.value &&
        e.target.stars.value &&
        e.target.discount.value &&
        e.target.image1.files[0] &&
        e.target.image2.files[0] &&
        e.target.image3.files[0] &&
        e.target.image4.files[0]
      ) {
        let form_data = new FormData();
        form_data.append("name", e.target.name.value);
        form_data.append("description", e.target.description.value);
        form_data.append("slug", e.target.slug.value);
        form_data.append("amount", e.target.amount.value);
        form_data.append("category", e.target.category.value);
        form_data.append("delivery", e.target.delivery.value);
        form_data.append("price", e.target.price.value);
        form_data.append("stars", e.target.stars.value);
        form_data.append("sold", e.target.sold.value);
        form_data.append("discount", e.target.discount.value);
        form_data.append("cover1", e.target.image1.files[0]);
        form_data.append("cover2", e.target.image2.files[0]);
        form_data.append("cover3", e.target.image3.files[0]);
        form_data.append("cover4", e.target.image4.files[0]);
        const response = await axios.post(
          "http://127.0.0.1:8000/api/products/",
          form_data,
          {
            headers: {
              "content-type": "multipart/form-data",
            },
          }
        );
        dispatch(creationSuccess(response.data));
        navigate("/");
      } else {
        setValidation("Some fields are required");
      }
    } catch (error) {
      dispatch(creationFailure(error.response.data));
    }
  };

  return (
    <>
      <form
        className="addprd"
        encType="multipart/form-data"
        onSubmit={(e) => handleSubmit(e)}
      >
        {errors !== null &&
          errorMessage().map((error) => (
            <div
              className="alert alert-danger m-1 p-1 text-start"
              role="alert"
              key={error}
            >
              {error}
            </div>
          ))}
        {validation && (
          <div className="alert alert-danger m-1 p-1 text-start" role="alert">
            {validation}
          </div>
        )}
        <input required placeholder="name" name="name" />
        <input required placeholder="description" name="description" />
        <input required placeholder="slug" name="slug" />
        <input required placeholder="amount" name="amount" />
        <input required placeholder="category" name="category" />
        <input
          required
          placeholder="delivery"
          type="checkbox"
          name="delivery"
        />
        <input required type="number" name="price" placeholder="price" />
        <input required placeholder="stars" name="stars" type="number" />
        <input placeholder="sold" name="sold" type="number" />
        <input type="number" name="discount" placeholder="discount" />
        <input
          required
          type="file"
          accept="image/png, image/jpg, image/jpeg, image/wepb"
          name="image1"
          placeholder="image1"
        />
        <input
          required
          type="file"
          accept="image/png, image/jpg, image/jpeg, image/wepb"
          name="image2"
          placeholder="image2"
        />
        <input
          required
          type="file"
          accept="image/png, image/jpg, image/jpeg, image/wepb"
          name="image3"
          placeholder="image3"
        />
        <input
          required
          type="file"
          accept="image/png, image/jpg, image/jpeg, image/wepb"
          name="image4"
          placeholder="image4"
        />
        <button disabled={status === "loading"} type="submit">
          {status === "loading" ? "loading..." : "Submit"}
        </button>
      </form>
    </>
  );
};

export default AddProduct;
