import { Tab, Tabs, TabList, TabPanel } from "react-tabs";
import "react-tabs/style/react-tabs.css";
import "./Authorization.css";
import { useSelector } from "react-redux";
import { userStatus, userErrors } from "../../Redux/Products/UserSlice";
import { Login, Register } from "../../utils/login";
import { useHistory } from "react-router-dom";
import { useCallback } from "react";

const Authorization = () => {
  const history = useHistory();
  const loginStatus = useSelector(userStatus);
  const error = useSelector(userErrors);

  const errorMessage = useCallback(() => {
    return Object.keys(error).map((name) => {
      const msg = error[name].join(", ");
      return `${name} - ${msg}`;
    });
  }, [error]);

  return (
    <div className="container_login">
      <Tabs selectedTabClassName="active_tab">
        <TabList className="tab__list">
          <Tab className="tab">Register</Tab>
          <Tab className="tab">Login</Tab>
        </TabList>

        <TabPanel className="tabPanel">
          <h3 className="tab__label">register form</h3>
          <form onSubmit={(e) => Register(e, history)}>
            <div className="def_input">
              <label>Full Name *</label>
              <input
                required
                name="regname"
                type="text"
                placeholder="Ex. John Doe"
              />
            </div>

            <div className="def_input">
              <label>Username *</label>
              <input
                required
                name="regusername"
                type="text"
                placeholder="Ex. John Doe"
              />
            </div>

            <div className="def_input">
              <label>Email *</label>
              <input
                required
                name="regemail"
                type="email"
                placeholder="Ex. johndoe@site.com"
              />
            </div>
            <div className="def_input">
              <label>Password *</label>
              <input required name="regpass" type="password" />
            </div>

            <div className="def_input">
              <label>Confirm Password *</label>
              <input name="repass" required type="password" />
            </div>
            <button className="def_button" disabled={loginStatus === "loading"}>
              {loginStatus === "loading" ? "loading ..." : "Submit"}
            </button>
            {error !== null &&
              errorMessage().map((error) => (
                <p className="form__errors" role="alert" key={error}>
                  {error}
                </p>
              ))}
          </form>
        </TabPanel>

        <TabPanel className="tabPanel">
          <h3 className="tab__label">login form</h3>

          <form onSubmit={(e) => Login(e, history)}>
            <div className="def_input">
              <label>Username *</label>
              <input name="loginemail" placeholder="Ex. johndoe" />
            </div>
            <div className="def_input">
              <label>Password *</label>
              <input name="loginpassword" type="password" />
            </div>

            <button className="def_button" disabled={loginStatus === "loading"}>
              {loginStatus === "loading" ? "loading ..." : "Submit"}
            </button>
            {error !== null &&
              errorMessage().map((error) => (
                <p className="form__errors" role="alert" key={error}>
                  {error}
                </p>
              ))}
          </form>
        </TabPanel>
      </Tabs>
    </div>
  );
};

export default Authorization;
