import { createSlice, createAsyncThunk } from "@reduxjs/toolkit";
import axios from "axios";
const initialState = {
  products: [],
  status: "idle",
  error: null,
  creationStatus: "idle",
};

export const fetchProducts = createAsyncThunk(
  "products/fetchProducts",
  async () => {
    const response = await axios.get("/api/products/");
    return response.data;
  }
);

const productsSlice = createSlice({
  name: "products",
  initialState,
  reducers: {
    creationStart: (state, action) => {
      state.creationStatus = "loading";
    },
    creationSuccess: (state, action) => {
      state.creationStatus = "success";
      state.products = state.products.concat(action.payload);
    },
    creationFailure: (state, action) => {
      state.creationStatus = "failed";
      state.error = action.payload;
    },
  },
  extraReducers(builder) {
    builder
      .addCase(fetchProducts.pending, (state) => {
        state.status = "loading";
      })

      .addCase(fetchProducts.fulfilled, (state, action) => {
        state.status = "success";
        state.products = state.products.concat(action.payload);
      })
      .addCase(fetchProducts.rejected, (state, action) => {
        state.status = "failed";
        state.error = action.error.message;
      });
  },
});

export default productsSlice.reducer;

export const getSlideProducts = (state) => state.products.products.slice(-5);
export const getAllProducts = (state) => state.products.products;
export const getBySlug = (state, slug) =>
  state.products.products.find((product) => product.slug === slug);

export const getByID = (state, id) =>
  state.products.products.find((product) => product.id === id);

export const getByCategory = (state, category) =>
  state.products.products.filter((product) => product.category === category);

export const getProductStatus = (state) => state.products.status;
export const getProductError = (state) => state.products.error;

export const creationStatus = (state) => state.products.creationStatus;
export const creationError = (state) => state.products.error;

export const { creationStart, creationFailure, creationSuccess } =
  productsSlice.actions;
