import axios from "axios";
import { toast } from "react-toastify";
import {
  userFailure,
  userStart,
  userSuccess,
} from "../Redux/Products/UserSlice";
import jwtDecode from "jwt-decode";
import { store } from "../Redux/store";

export const LoginSet = () => {
  const user = localStorage.getItem("user");
  if (user) {
    store.dispatch(userSuccess(jwtDecode(user)));
  }
};

export const Register = async (e, history) => {
  e.preventDefault();
  store.dispatch(userStart());
  const email = e.target.regemail.value;
  const name = e.target.regname.value;
  const username = e.target.regusername.value;
  const password = e.target.regpass.value;
  const repass = e.target.repass.value;

  if (email && name && username && password && repass) {
    if (password === repass) {
      try {
        const response = await axios.post("/api/user/", {
          username,
          email,
          first_name: name,
          password,
        });
        if (response.status === 200) {
          const login = await axios.post("/api/token/", {
            username,
            password,
          });
          store.dispatch(userSuccess(jwtDecode(login.data.access)));
          localStorage.setItem("user", JSON.stringify(login.data));
          toast.success("Signed in successfuly");
          history.push("/");
        } else {
          toast.error("Something went wrong");
          store.dispatch(userSuccess(null));
          localStorage.removeItem("user");
          store.dispatch(userFailure(response));
        }
      } catch (error) {
        toast.error("Something went wrong");
        store.dispatch(userSuccess(null));
        localStorage.removeItem("user");
        const { data } = error.response;
        const err = Object.values(data);
        store.dispatch(userFailure({ error: err }));
      }
    } else {
      store.dispatch(userFailure({ error: ["Passwords do not match"] }));
      toast.error("Registration failed");
    }
  } else {
    toast.error("Authorization went wrong");
    store.dispatch(userFailure({ error: ["All fields are required"] }));
  }
};

export const Login = async (e, history) => {
  e.preventDefault();
  store.dispatch(userStart());
  const email = e.target.loginemail.value;
  const password = e.target.loginpassword.value;

  if (email && password) {
    try {
      const response = await axios.post("/api/token/", {
        username: email,
        password: password,
      });
      store.dispatch(userSuccess(jwtDecode(response.data.access)));
      localStorage.setItem("user", JSON.stringify(response.data));
      history.push("/");
      toast.success("Signed in successfuly");
    } catch (error) {
      toast.error("Something went wrong");
      store.dispatch(userSuccess(null));
      localStorage.removeItem("user");
      const { data } = error.response;
      const err = Object.values(data);
      store.dispatch(userFailure({ error: err }));
    }
  } else {
    toast.error("Authorization went wrong");
    store.dispatch(userFailure({ error: ["All fields are required"] }));
  }
};

export const logoutUser = () => {
  store.dispatch(userSuccess(null));
  localStorage.removeItem("user");
  toast.info("Signed out");
};

export const updateToken = async () => {
  let user = JSON.parse(localStorage.getItem("user"));

  try {
    let response = await axios.post("/api/token/refresh/", {
      refresh: user.refresh,
    });

    let data = await response.data;
    store.dispatch(userSuccess(jwtDecode(data.access)));
    localStorage.setItem("user", JSON.stringify(data));
  } catch (error) {
    const err = error.response.data.detail;
    toast.error(err);
    store.dispatch(userFailure(err));
    logoutUser();
  }
};
