import React from "react";
import ContentLoader from "react-content-loader";
import useWindowDimensions from "../../utils/windowSize";

const ProductsLoader = () => {
  let { width } = useWindowDimensions();
  width = (width / 100) * 93;
  let x = (width / 100) * 0;
  return (
    <>
      <ContentLoader viewBox={`${x} 0 ${width} 340`} height={340} width={width}>
        <rect x="0" y="0" rx="0" ry="0" width="290" height="250" />
        <rect x="0" y="259" rx="0" ry="0" width="290" height="15" />
        <rect x="0" y="281" rx="0" ry="0" width="140" height="15" />

        <rect x="345" y="0" rx="0" ry="0" width="290" height="250" />
        <rect x="345" y="259" rx="0" ry="0" width="290" height="15" />
        <rect x="345" y="281" rx="0" ry="0" width="140" height="15" />

        <rect x="695" y="0" rx="0" ry="0" width="290" height="250" />
        <rect x="695" y="259" rx="0" ry="0" width="290" height="15" />
        <rect x="695" y="281" rx="0" ry="0" width="140" height="15" />

        <rect x="1030" y="0" rx="0" ry="0" width="290" height="250" />
        <rect x="1030" y="259" rx="0" ry="0" width="290" height="15" />
        <rect x="1030" y="281" rx="0" ry="0" width="140" height="15" />
      </ContentLoader>
    </>
  );
};

export default ProductsLoader;
