import React from "react";
import { useParams } from "react-router-dom";
import {
  getCategoriesBySlug,
  getCategoriesStatus,
} from "../Redux/Products/CategorySlice";
import CategoryProducts from "../Components/Products/CategoryProducts";
import Header from "../Components/Header/Header";
import Footer from "../Components/Footer/Footer";
import {
  getProductError,
  getProductStatus,
  getByCategory,
} from "../Redux/Products/Products";
import { useSelector } from "react-redux";

const CategoryScreen = () => {
  const { slug } = useParams();
  const catStatus = useSelector(getCategoriesStatus);
  const categorySelector = useSelector((state) =>
    getCategoriesBySlug(state, slug)
  );
  let category;
  let products;

  if (catStatus === "loading") {
    category = false;
  } else if (catStatus === "success") {
    category = categorySelector;
  }

  const productSelector = useSelector((state) =>
    getByCategory(state, category.id)
  );
  const productStatus = useSelector(getProductStatus);
  const productError = useSelector(getProductError);

  if (productStatus === "loading") {
    products = false;
  } else if (productStatus === "success") {
    products = productSelector;
  }

  return (
    <>
      <Header />
      <CategoryProducts
        status={productStatus}
        error={productError}
        products={products}
        category={category}
      />
      <Footer />
    </>
  );
};

export default CategoryScreen;
