import { Link } from "react-router-dom";
import "./Products.css";
import {
  getAllProducts,
  getProductStatus,
} from "../../Redux/Products/Products";
import { useSelector } from "react-redux";
import { setCartItems } from "../../utils/cartActions";
import ProductsLoader from "./ProductsLoader";

const Products = () => {
  const pstatus = useSelector(getProductStatus);
  const products = useSelector(getAllProducts);
  const sortedDiscounts = []
    .concat(products)
    .sort(function compare(a, b) {
      return a.discount - b.discount;
    })
    .slice(-4);
  const sortedNews = []
    .concat(products)
    .sort(function dateComparision(a, b) {
      var dateA = new Date(a.created_at);
      var dateB = new Date(b.created_at);
      return dateA - dateB;
    })
    .slice(-12);

  const sortedTops = []
    .concat(products)
    .sort(function dateComparision(a, b) {
      var dateA = new Date(a.sold);
      var dateB = new Date(b.sold);
      return dateA - dateB;
    })
    .slice(-12);
  return (
    <div>
      <div className="container-fill products__list">
        <div className="container__label">New discounts</div>

        {pstatus === "loading" ? (
          <ProductsLoader />
        ) : (
          <div className="products__content">
            {sortedDiscounts.reverse().map((product, index) => (
              <div className="product__item" key={index}>
                <div className="product__item_header">
                  <div>new</div>
                  <div>-{product.discount}%</div>
                </div>
                <div className="product__item_cover">
                  <Link to={`/product/${product.slug}`}>
                    <img src={`${product.cover1}`} alt={product.name} />
                  </Link>
                </div>
                <div className="product__item_info">
                  <div className="product__item_label">
                    <Link to={`/product/${product.slug}`}>{product.name}</Link>
                  </div>
                  <div className="product__item_price">
                    <div
                      className="product__item_atc"
                      onClick={() => setCartItems(product.id, 1)}
                    >
                      add to cart
                    </div>
                    <div className="product__item_prices">
                      <strike>${product.price}</strike> $
                      {product.price - (product.price / 100) * product.discount}
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      <div className="container-fill products__list">
        <div className="container__label">New products</div>
        {pstatus === "loading" ? (
          <ProductsLoader />
        ) : (
          <div className="products__content">
            {sortedNews.reverse().map((product, index) => (
              <div className="product__item" key={index}>
                <div className="product__item_header">
                  <div>new</div>
                  <div>-{product.discount}%</div>
                </div>
                <div className="product__item_cover">
                  <Link to={`/product/${product.slug}`}>
                    <img src={`${product.cover1}`} alt={product.name} />
                  </Link>
                </div>
                <div className="product__item_info">
                  <div className="product__item_label">
                    <Link to={`/product/${product.slug}`}>{product.name}</Link>
                  </div>
                  <div className="product__item_price">
                    <div
                      className="product__item_atc"
                      onClick={() => setCartItems(product.id, 1)}
                    >
                      add to cart
                    </div>
                    <div className="product__item_prices">
                      <strike>${product.price}</strike> $
                      {product.price - (product.price / 100) * product.discount}
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>

      <div className="container-fill products__list">
        <div className="container__label">Top products</div>
        {pstatus === "loading" ? (
          <ProductsLoader />
        ) : (
          <div className="products__content">
            {sortedTops.reverse().map((product, index) => (
              <div className="product__item" key={index}>
                <div className="product__item_header">
                  <div>new</div>
                  <div>-{product.discount}%</div>
                </div>
                <div className="product__item_cover">
                  <Link to={`/product/${product.slug}`}>
                    <img src={`${product.cover1}`} alt={product.name} />
                  </Link>
                </div>
                <div className="product__item_info">
                  <div className="product__item_label">
                    <Link to={`/product/${product.slug}`}>{product.name}</Link>
                  </div>
                  <div className="product__item_price">
                    <div
                      className="product__item_atc"
                      onClick={() => setCartItems(product.id, 1)}
                    >
                      add to cart
                    </div>
                    <div className="product__item_prices">
                      <strike>${product.price}</strike> $
                      {product.price - (product.price / 100) * product.discount}
                    </div>
                  </div>
                </div>
              </div>
            ))}
          </div>
        )}
      </div>
    </div>
  );
};

export default Products;
