import "./Header.css";
import { useState } from "react";
import { Link } from "react-router-dom";
import { useSelector } from "react-redux";
import {
  getCategories,
  getCategoriesErrors,
  getCategoriesStatus,
} from "../../Redux/Products/CategorySlice";
import {
  getAllProducts,
  getProductStatus,
} from "../../Redux/Products/Products";
import { getCartItem } from "../../Redux/Products/CartSlice";
import { removeCartItem } from "../../utils/cartActions";
import { user } from "../../Redux/Products/UserSlice";
import { logoutUser } from "../../utils/login";

const Header = () => {
  const status = useSelector(getCategoriesStatus);
  const error = useSelector(getCategoriesErrors);
  const categories = useSelector(getCategories);
  const productsSelector = useSelector(getAllProducts);
  const productStatus = useSelector(getProductStatus);
  const cartProducts = [];
  const getUser = useSelector(user);

  let content = <p>Loading ... </p>;
  let amount = 0;
  const cartItems = useSelector(getCartItem);

  if (cartItems) {
    for (let i = 0; i < cartItems.length; i++) {
      amount += cartItems[i].amount;
    }
  }

  if (productStatus === "success" && cartItems) {
    for (let i = 0; i < cartItems.length; i++) {
      let product = productsSelector.find(
        (item) => item.id === cartItems[i].id
      );
      if (product) {
        cartProducts.push({ ...product, cartAmount: cartItems[i].amount });
      }
    }
  }

  if (status === "loading") {
    content = "loading ...";
  } else if (status === "success") {
    content = categories.map((category, index) => (
      <Link
        className="dropdown__item"
        key={index}
        to={`/category/${category.slug}/`}
      >
        {category.name}
      </Link>
    ));
  } else if (status === "failed") {
    content = <p>{error}</p>;
  }

  const [toggle, setToggle] = useState(false);
  const showMenu = () => {
    setToggle(!toggle);
  };
  return (
    <div className="container header">
      <div className="menu">
        <Link to="/" className="menu__item">
          home
        </Link>
        <div href="/" className="menu__item dropdown__menu">
          <Link to="/">categories</Link>
          <div className="dropdown dropdown__left">
            <div className="dropdown__item_label">Categories</div>

            {content}
          </div>
        </div>

        <a href="https://ogabeks.netlify.app" className="menu__item ">
          About developer
        </a>
      </div>
      <div className="brand">ULTRA</div>
      <div className="menu__utils">
        <div className="menu__item dropdown__menu cart__dropdown">
          <Link to="/cart">
            <i className="fa fa-shopping-cart"></i>
            Cart <span className="menu__item_sub">({amount})</span>
          </Link>

          <div className="dropdown dropdown__right">
            {amount ? (
              cartProducts.map((p, index) => (
                <div key={index} className="dropdown__cart_item">
                  <div>
                    <img src={`${p.cover1}`} alt={p.name} />
                  </div>
                  <div className="cart__item_info">
                    <div
                      className="cart__item_close"
                      onClick={() => removeCartItem(p.id)}
                    >
                      
                    </div>
                    <div className="cart__item_label">
                      <Link
                        to={`/product/${p.slug}`}
                        className="cart__item_link"
                      >
                        {p.name}
                      </Link>
                    </div>
                    <div className="cart__item_amount">
                      {p.cartAmount} x $
                      {p.discount
                        ? p.price - (p.price / 100) * p.discount
                        : p.price}
                    </div>
                  </div>
                </div>
              ))
            ) : (
              <p>No products yet</p>
            )}
          </div>
        </div>

        {getUser ? (
          <div className="menu__item dropdown__menu">
            <div>
              <i className="fa fa-user"></i>
              {getUser.username}
            </div>
            <div className="dropdown dropdown__left">
              <div className="dropdown__item">{getUser.username}</div>
              <div className="dropdown__item" onClick={() => logoutUser()}>
                Sign out
              </div>
            </div>
          </div>
        ) : (
          <Link to="/user" className="menu__item">
            <i className="fa fa-user-o"> </i>
            Login
          </Link>
        )}
      </div>
      <div className="mobile__menu" onClick={() => showMenu()}>
        menu <i className="fa fa-bars"></i>
      </div>

      <div
        className="sideMenu"
        style={toggle ? { display: "block" } : { display: "none" }}
      >
        <div className="sideMenu__close" onClick={() => showMenu()}>
          <i className="fa fa-close"></i>
        </div>
        <div className="sideMenu__label">ULTRA</div>

        <div className="menu__item ">
          <Link to="/">home</Link>
        </div>

        <div className="menu__item dropdown__menu">
          <Link to="/">categories</Link>
          <div className="dropdown dropdown__left">{content}</div>
        </div>
        <div className="menu__item">
          <Link to="/cart">
            <i className="fa fa-shopping-cart"></i>
            Cart <span className="menu__item_sub">({amount})</span>
          </Link>
        </div>

        {getUser ? (
          <div className="menu__item dropdown__menu">
            <div>
              <i className="fa fa-user"></i>{getUser.username}
            </div>
            <div className="dropdown dropdown__right">
              <div className="dropdown__item">{getUser.username}</div>
              <div className="dropdown__item" onClick={() => logoutUser()}>
                Sign out
              </div>
            </div>
          </div>
        ) : (
          <Link to="/user" className="menu__item">
            <i className="fa fa-user-o"> </i>
            Login
          </Link>
        )}
      </div>
    </div>
  );
};

export default Header;
