import "./MainSlide.css";
import { Swiper, SwiperSlide } from "swiper/react";
import "swiper/css";
import "swiper/css/pagination";
import "swiper/css/navigation";
import { Autoplay, Pagination } from "swiper";
import {
  getSlideProducts,
  getProductError,
  getProductStatus,
} from "../../Redux/Products/Products";
import { useSelector } from "react-redux";
import MainSlideLoader from "./MainSlideLoader";

const MainSlide = () => {
  const products = useSelector(getSlideProducts);
  const status = useSelector(getProductStatus);

  let content;
  if (status === "loading") {
    content = <MainSlideLoader />;
  } else if (status === "success") {
    content = (
      <Swiper
        centeredSlides={true}
        autoplay={{
          delay: 6000,
          disableOnInteraction: false,
        }}
        pagination={{
          clickable: true,
          type: "progressbar",
          progressbarFillClass: "customProgres",
        }}
        loop={true}
        modules={[Autoplay, Pagination]}
        className="container main__slide"
      >
        {products.map((product, index) => (
          <SwiperSlide key={index} className="slide__content">
            {({ isActive }) => (
              <div className="slide__item">
                <div className="slide__item_info">
                  <h1
                    style={
                      isActive ? { display: "block" } : { display: "none" }
                    }
                  >
                    {product.name}
                  </h1>
                  <p
                    style={
                      isActive ? { display: "block" } : { display: "none" }
                    }
                  >
                    {product.description}
                  </p>
                </div>
                <div className="slide__item_cover">
                  <img
                    style={
                      isActive ? { display: "block" } : { display: "none" }
                    }
                    src={`${product.cover1}`}
                    alt="img"
                  />
                </div>
              </div>
            )}
          </SwiperSlide>
        ))}
      </Swiper>
    );
  } else if (status === "failed") {
    content = <p>Error : {getProductError}</p>;
  }

  return <>{content}</>;
};

export default MainSlide;
