import React from "react";
import "./assets/css/Main.css";
import { HashRouter as Router, Route, Switch } from "react-router-dom";
import HomeScreen from "./Screens/HomeScreen";
import SingleProductScreen from "./Screens/SingleProductScreen";
import CartScreen from "./Screens/CartScreen";
import AuthorizationScreen from "./Screens/AuthorizationScreen";
import AddProduct from "./Screens/AddProduct";
import ScrollToTop from "./utils/Scroll";
import CategoryScreen from "./Screens/CategoryScreen";
import { ToastContainer } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";
import { useEffect } from "react";
import { updateToken } from "./utils/login";
import { useSelector } from "react-redux";
import LoggedRoute from "./utils/LoggedRoute";

function App() {
  const user = useSelector((state) => state.user.user);
  useEffect(() => {
    if (user) {
      let fourMinutes = 1000 * 60 * 4;
      let interval = setInterval(() => {
        updateToken();
      }, fourMinutes);
      return () => clearInterval(interval);
    }
  }, [user]);
  return (
    <Router>
      <ScrollToTop />
      <Switch>
        <Route path="/" component={HomeScreen} exact />
        <Route path="/product/:slug/" component={SingleProductScreen} />
        <Route path="/cart/" component={CartScreen} />
        <LoggedRoute component={AuthorizationScreen} path="/user/" />
        <Route path="/add/" component={AddProduct} />
        <Route path="/category/:slug/" component={CategoryScreen} />
      </Switch>
      <ToastContainer />
    </Router>
  );
}

export default App;
