import "./Cart.css";
import { useSelector } from "react-redux";
import {
  getAllProducts,
  getProductStatus,
} from "../../Redux/Products/Products";
import { getCartItem } from "../../Redux/Products/CartSlice";
import { removeCartItem, setCartItems } from "../../utils/cartActions";

const Cart = () => {
  const productsSelector = useSelector(getAllProducts);
  const productStatus = useSelector(getProductStatus);
  const cartProducts = [];
  const cartItems = useSelector(getCartItem);

  if (productStatus === "success" && cartItems) {
    for (let i = 0; i < cartItems.length; i++) {
      let product = productsSelector.find(
        (item) => item.id === cartItems[i].id
      );
      if (product) {
        cartProducts.push({ ...product, cartAmount: cartItems[i].amount });
      }
    }
  }

  return (
    <>
      <div className="container-fill carts">
        <div className="container__label">shopping cart</div>

        {cartProducts.length > 0 ? (
          cartProducts.map((product, index) => (
            <div key={index} className="cart__card">
              <div className="card__cover">
                <img src={`${product.cover1}`} alt={product.name} />
              </div>
              <div className="card__title">{product.name}</div>
              <div className="card__price">
                {product.discount ? (
                  <>
                    $<strike>{product.price}</strike>
                    <br />
                    {product.price - (product.price / 100) * product.discount}
                  </>
                ) : (
                  <>${product.price}</>
                )}
              </div>
              <div className="card__qty">
                {product.cartAmount > 1 && (
                  <button onClick={() => setCartItems(product.id, -1)}>
                    -
                  </button>
                )}
                <input readOnly value={product.cartAmount} />
                <button onClick={() => setCartItems(product.id, 1)}>+</button>
              </div>
              <div className="card__total">
                {}$
                {product.cartAmount *
                  (product.price - (product.price / 100) * product.discount)}
              </div>
              <div className="card__close">
                <i
                  onClick={() => removeCartItem(product.id)}
                  className="fa fa-trash"
                ></i>
              </div>
            </div>
          ))
        ) : (
          <p>Error</p>
        )}
      </div>
    </>
  );
};

export default Cart;
