import { Star } from "feather-icons-react";

function Stars({ num, size = 24 }) {
  const myStars = [];
  const unStars = [];
  let content;
  let unContent;
  for (let index = 0; index < Number(num); index++) {
    myStars.push(index);
  }

  content = myStars.map((star) => (
    <Star size={size} fill="#ffdc17" stroke="#ffdc17" key={star} />
  ));

  if (5 - num > 0) {
    for (let i = 0; i < 5 - num; i++) {
      unStars.push(i);
    }
    unContent = unStars.map((star) => (
      <Star size={size} stroke="#ffdc17" key={star} />
    ));
  }

  return (
    <>
      {console.log(content)}
      {content}
      {unContent}
    </>
  );
}

export default Stars;
